<template>
  <div>
    <v-container v-if="$route.name == 'data-access-strategy'">
      <mrc_ess_table :headers='headers'
                     pageName='data-access-strategy-add'
                     :paginationOptions='paginationOptions'
                     @turnPage='getList'
                     :isLoading='loading'
                     :slotKey="['strategy_number']"
                     :items='desserts'>

        <template v-slot:item.strategy_number='{item}'>
          <!-- {{item.strategy_number}} -->
          <template v-for="(it,index) in item.strategy_number.split(',')">
            <v-chip v-if="it"
                    :key="index">{{it}}</v-chip>
          </template>
        </template>

        <template v-slot:item.actions='{item}'>
          <menu-list :list='list'
                     @handleClick='handleClick($event,item)'>
            <template v-slot:showAction>
              <v-icon>mdi-dots-vertical</v-icon>
            </template>
          </menu-list>
        </template>
      </mrc_ess_table>
    </v-container>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import { listData } from '@/service/api/data-access-strategy'
export default {
  components: {},
  props: {},
  data () {
    return {
      desserts: [],
      loading: false,
      paginationOptions: {
        pagesize: this.$store.state.pageSize,
        cpage: this.$store.state.cpage,
        totalCount: 0, //总条数
        totalpage: 0, //总页数
      },
    };
  },
  validations: {},
  computed: {
    list () {
      return [
        {
          name: this.$t('label_View')
        },
        {
          name: this.$t('label_Edit')
        },
        {
          name: this.$t('label_delete')
        }
      ]
    },
    headers () {
      return [
        {
          text: '存储过程名称',
          align: 'start',
          sortable: false,
          value: 'org_level_uuid',
        },
        {
          text: '存储过程名称',
          align: 'start',
          sortable: false,
          value: 'strategy_number',
        },
        {
          text: ' ',
          align: 'start',
          sortable: false,
          value: 'actions',
          width: '20'
        }
      ]
    }
  },
  created () { },
  watch: {
    async '$route' (val) {
      if (val.name == 'approver') {
        await this.getList(this.paginationOptions)
      }
    }
  },
  async mounted () {
    await this.getList()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    async getList (obj) {
      if (obj) {
        this.paginationOptions = obj
      }
      this.loading = true
      this.desserts = []
      const res = await listData({
        cpage: this.paginationOptions.cpage,
        pagesize: this.paginationOptions.pagesize,
        // org_level_uuid: this.$store.state.org_level_uuid
        org_level_uuid: 'bc411def-e0e5-4ea2-ae58-e4bec8ac247e'
      })
      if (res.code == 200) {

        this.desserts = res.data
        this.paginationOptions.totalCount = res.totalCount
        this.paginationOptions.totalpage = res.totalpage
      }
      this.loading = false
    },
    handleClick ({ index }, item) {
      if (index == 0) {
        this.$store.commit('set_subPage_name', item.org_level_uuid)
        this.$store.commit('set_uuid', item.uuid)
        this.$store.commit('set_iconstatus', true)  //行内进图标显示
        this.$router.push({
          name: 'data-access-strategy-detail',
          params: {
            name: item.org_level_uuid,
            uuid: item.uuid
          }
        })
      }
      // 编辑
      if (index == 1) {
        this.$store.commit('set_subPage_name', item.org_level_uuid)
        this.$store.commit('set_uuid', item.uuid)
        this.$store.commit('set_iconstatus', false)
        this.$router.push({
          name: 'data-access-strategy-detail',
          params: {
            name: item.org_level_uuid,
            uuid: item.uuid
          }
        })
      }
      if (index == 2) {
        this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
          .then(async () => {
            const del = await delProcedure({
              uuid: item.uuid
            })
            if (del.code == 200) {
              this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
              // this.desserts.length === 1  只有一条数据 删除就请求上一页的数据
              if (this.desserts.length === 1 && this.paginationOptions.cpage > 1) {
                this.paginationOptions.cpage--
              }
              await this.getList()
            }
          }).catch(() => { })
      }
    },
  },
};
</script>

<style scoped>
.procedure_name {
  display: flex;
  flex-direction: column;
  padding: 10px 0;
}
.name {
  font-size: 16px;
}
.desc {
  color: #aaaaaa;
}
</style>
